"use strict";
import axios from 'axios';

function	createAxios() {
	const api = axios.create({
		headers: {
			'Allow-Control-Allow-Origin': '*',
			'crossDomain': 'true',
			'Content-Type': 'application/json',
		}
	});
	return(api);
}

export	function	getAnswer(question, context) {
	const api = createAxios();
	return(api.get('https://greenfly.ai:3002/ask', {
		params: {
			context: context,
			question: question
		}
	}));
}