<template>
	<v-app>
		<v-container fluid>
			<v-layout row wrap>
				<v-flex md-12 xs-12>
					<v-textarea
						outline
						auto-grow
						counter
						name="input"
						label="Copy and Paste the text that you want to search."
						ref="context"
						v-model="context"
						hint=""
						:rules="[rules.required, rules.max]"
						maxlength="2000"
						min-width="800"
						required
						:loading="this.loading"
					></v-textarea>
				</v-flex>
			</v-layout>
			<v-spacer/>
			<v-layout row wrap>
				 <v-flex md-12 xs-12>
					<v-text-field
						@keydown="handleKey($event)"
						outline
						ref="question"
						counter
						v-model="question"
						label="Enter Your question here"
						maxlength="2000"
						min-width="800"
						:rules="[rules.max]"
						required
						:loading="this.loading"
					></v-text-field>
				</v-flex>
			</v-layout>
			<v-layout row wrap>
				<v-flex md-12 xs-12>
					<v-slide-y-reverse-transition>
						<v-card
							class="mx-auto"
							color="#279090"
							dark
							max-width="400"
							min-height="200"
							v-show="this.answer"
						>
							<v-card-title>
								<span class="title font-weight-light">{{ this.q_two }}</span>
							</v-card-title>
							<v-card-text>
								<span class="headline font-weight-bold">{{ this.answer }}</span>
							</v-card-text>
						</v-card>
					</v-slide-y-reverse-transition>
					<v-progress-circular
						v-show="this.loading"
      			indeterminate
      			color="#279090"
      			:size="70"
    			>Please Wait.</v-progress-circular>
				</v-flex>
			</v-layout>
			<v-layout row wrap>
				<v-flex md-12 xs-12>
						<v-alert
							:value="this.error"
							type="error"
							dismissible
							transition="slide-y-reverse-transition"
							outline
						>
							Please fill in both fields.
						</v-alert>
				</v-flex>
			</v-layout>
		</v-container>
	</v-app>
</template>

<script>

import { getAnswer } from '../api.js'
export default {
	name: 'Search',
	data() {
		return {
			context: "Hendrik Johannes Cruijff OON (anglicised to Cruyff; 25 April 1947 – 24 March 2016) was a Dutch professional football player and coach. As a player, he won the Ballon d'Or three times, in 1971, 1973, and 1974 Cruyff was the most famous exponent of the football philosophy known as Total Football explored by Rinus Michels, and is widely regarded as one of the greatest players in football history. In the late 1960s and early 1970s, Dutch football rose from obscurity to become a powerhouse in the sport. Cruyff led the Netherlands to the final of the 1974 FIFA World Cup and received the Golden Ball as player of the tournament. At the 1974 finals, he executed a feint that subsequently was named after him, the 'Cruyff Turn', a move widely replicated in the modern game. Wearing the number 14 jersey, he set a trend for wearing shirt numbers outside the usual starting line-up numbers of one to eleven. At club level, Cruyff started his career at Ajax, where he won eight Eredivisie titles, three European Cups and one Intercontinental Cup. In 1973, he moved to Barcelona for a world record transfer fee, winning La Liga in his first season and was named European Footballer of the Year. After retiring from playing in 1984, Cruyff became highly successful as manager of Ajax and later Barcelona; he remained an influential advisor to both clubs. His son Jordi also played football professionally. In 1999, Cruyff was voted European Player of the Century in an election held by the International Federation of Football History & Statistics, and came second behind Pelé in their World Player of the Century poll. He came third in a vote organised by the French magazine France Football consulting their former Ballon d'Or winners to elect their Football Player of the Century. He was chosen on the World Team of the 20th Century in 1998, the FIFA World Cup Dream Team in 2002, and in 2004 was named in the FIFA 100 list of the world's greatest living players.",
			question: "",
			q_two: "",
			answer: "",
			rules: {
				required: value => !!value || 'Required.',
				max: v => v.length <= 2000 || 'Max 2000 characters'
			},
			error: false,
			loading: false
		}
	},
	methods: {
		async submit(question) {
			if (question !== '' && this.context !== '') {
				this.loading = true
				const resp = await getAnswer(question, this.context)
				if (await resp.status === 200) {
					this.loading = false
					if (JSON.parse(resp.data)["0"] === "") {
						this.answer = "We don't think that the answer to this question is contained within the text."
					}
					else {
						this.answer = JSON.parse(resp.data)["0"]
					}
				}
				else {
					this.loading = false
				}
			}
			else {
				this.error = true;
				setTimeout(() => { this.error = false }, 3000);
			}
		},
		handleKey(event) {
			if (event.keyCode === 13
				&& !event.shiftKey && this.loading === false) {
				this.answer = ''
				this.submit(this.question)
				this.q_two = this.question
				this.question = ''
			}
			else if (event.keyCode === 13 && !event.shiftKey) {
				event.preventDefault()
			}
		},
	}
}
</script>

